// font-family: 'Open Sans', sans-serif;
// font-family: 'Poppins', sans-serif;
// font-sizes: Open Sans - 400 600 700
// font-sizes: Open Sans - 700

h1 {
  font-size: 2rem;
  text-align: center;
}
.h1,
.h2--dark,
.h3--dark {
  color: $Very-Dark-Cyan;
}
.h1,
.h2,
.h3 {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
}
.para {
  font-family: "Open Sans", sans-serif;
}
.para--dark {
  color: $Very-Dark-Cyan;
}
.para--light {
  color: $light-grey;
}
